        this.HTMLcontent='<h3>POLTICA DE PRIVACIDAD MULTIPERFILES SOCIEDAD ANONIMA</h3> <p>MULTIPERFILES, SOCIEDAD ANNIMA, sociedad guatemalteca, de naturaleza mercantil, del domicilio de la ciudad y departamento de Guatemala, en adelante bajo el nombre de la aplicacin "Cultura Multigroup" se compromete a proteger su privacidad. A continuacin, exponemos como tratamos los datos de nuestros usuarios, y como protegemos su privacidad.</p> <p>Cultura Multigroup recopila informacin acerca de usted antes de acceder o usar nuestra aplicacin mvil, sitio web y otros productos y servicios online (conjuntamente, los Servicios) y a travs de otras interacciones y comunicaciones que tenga con nosotros. Recopilamos la informacin que usted nos proporciona directamente, por ejemplo, cuando crea o modifica su cuenta, solicita servicios bajo demanda, se pone en contacto con atencin al cliente o cuando se comunica de cualquier otra forma con nosotros. Esta informacin puede incluir, pero no limitarse a: nombre, correo electrnico, nmero de telfono, NIT, direccin postal, foto del perfil, mtodo de pago, los artculos solicitados para servicios de entrega, notas de entrega y otra informacin que usted elija proporcionar.</p> <p>Informacin que recopilamos a travs de su uso de nuestros Servicios Cuando utiliza nuestros Servicios, recopilamos informacin sobre usted en las siguientes categoras generales:</p> <ul> <li>Uso e informacin de preferencias: Recopilamos informacin sobre cmo usted y los visitantes del sitio interactan con nuestros Servicios, preferencias expresadas y configuraciones seleccionadas.</li> <li>Informacin del registro: Cuando usted interacta con los Servicios, recopilamos los registros del servidor, que pueden incluir informacin como direcciones IP del dispositivo, fechas y horas de acceso, funciones de la aplicacin o las pginas visitadas, bloqueos de la aplicacin y otra actividad del sistema, tipo de navegador y el sitio de terceros o servicio que estaba utilizando antes de interactuar con nuestros Servicio.</li> </ul> <p> <b>Uso de informacin</b>, Podemos usar la informacin que recopilamos sobre usted para:</p> <ul> <li>Prestar, mantener y mejorar nuestros Servicios como, por ejemplo, proporcionar productos y servicios que usted solicite (y enviar informacin relacionada), desarrollar nuevas funciones, prestar atencin al cliente tanto a los Usuarios como a nuestros colaboradores de Cultura Multigroup, desarrollar opciones de seguridad, autenticar usuarios y enviar actualizaciones de productos y mensajes administrativos.</li> <li>Realizar operaciones internas, como, por ejemplo, prevenir el fraude y el abuso de nuestros Servicios; solucionar los errores de software y problemas de funcionamiento; llevar a cabo anlisis de datos, pruebas e investigacin, controlar y analizar las tendencias de uso y de actividad.</li> <li>Enviarle comunicaciones que pensamos sern de su inters, incluida la informacin sobre los productos, servicios, promociones, noticias y eventos de Cultura Multigroup, y cuando est permitido y de conformidad con las leyes locales aplicables, para tramitar concursos, sorteos u otras entradas de promociones y cumplir con los premios relacionados.</li> <li>Personalizar y mejorar los Servicios, incluyendo proporcionar o recomendar funciones, contenido, conexiones sociales, referencias y anuncios.</li> <li>Cualquier uso que Cultura Multigroup, decida darle a dicha informacin.</li> </ul> <p><b>Intercambio de informacin</b>, Podemos compartir la informacin que recopilamos sobre usted tal como se describe a continuacin a travs de nuestros Servicios, podemos compartir su informacin:</p> <ul> <li>Con nuestro personal para que puedan prestar los Servicios que usted solicita. Por ejemplo, compartimos su nombre, foto (si la proporciona), y las ubicaciones de entrega.</li> <li>Con el pblico en general si enva el contenido a un foro pblico, por ejemplo, comentarios en blogs, mensajes de redes sociales u otras opciones de nuestros Servicios a disposicin del pblico en general.</li> <li>Con los terceros necesarios contratados por nosotros.</li> <li>Con proveedores, asesores, socios de marketing y otros proveedores de servicios que necesitan acceder a dicha informacin para llevar a cabo el trabajo en nuestro nombre.</li> <li>Cuando nos veamos obligados a suministrarla para atender una orden legtima o judicial de autoridad competente.</li> <li>En relacin con, o durante las negociaciones de, cualquier fusin, venta de activos de la empresa, consolidacin o reestructuracin, financiacin o adquisicin de la totalidad o una parte de nuestro negocio o en otra empresa.</li> <li>Si se lo notificamos y usted autoriza que se comparta.</li> <li>De forma agregada y/o annima que no pueda ser razonablemente utilizada para</li> identificarlo. </ul> <p> <b>Opcin de gestin. Informacin de cuenta</b><br> Usted puede acceder a la informacin de su cuenta en cualquier momento, as como a solicitar la actualizacin, modificacin y eliminacin de la informacin contenida en ella, de forma gratuita. Si desea cancelar su cuenta, envenos un mensaje de correo electrnico a info@multigroup.com.<br><br> Tenga en cuenta que en algunos casos podemos retener cierta informacin sobre usted como lo requiere la ley o para fines comerciales legtimos en la medida permitida por la ley. Por ejemplo, si usted tiene un crdito o deuda pendiente en su cuenta, o si creemos que ha cometido fraude o incumplido nuestros Trminos y Condiciones, podemos tratar de resolver el problema antes de eliminar su informacin.<br><br> <b>Cambios en las Polticas de Privacidad</b>, Multigroup, se reserva el derecho de modificar estas Polticas de Privacidad ocasionalmente. Si hacemos cambios importantes en la forma en que tratamos su informacin personal, se lo notificaremos a travs de los Servicios o por otros medios, como el correo electrnico. El uso continuado de los Servicios despus de dicha notificacin constituye su consentimiento a los cambios. Le recomendamos que revise peridicamente nuestra Poltica de Privacidad para tener la informacin ms reciente sobre nuestras prcticas.</p>'  
